<template>
  <div class="my">
    <div class="my-bg">
      <div class="my-bg-box">
        <div class="img-box">
          <img class="auto-img" :src="userInfo.userBg" alt  @click="bgShowPopup"/>
        </div>
      </div>
    </div>
    <div class="my-info">
      <div class="my-img" v-if="isLogin">
        <div class="img-box" @click="imgShowPopup">
          <img class="auto-img" :src="userInfo.userImg" alt />
        </div>
        <div class="my-info-text">
          <div class="nickname">{{userInfo.nickName}}</div>
          <div class="desc" v-if="userInfo.desc==''">这家伙很懒，什么也没有留下</div>
          <div class="desc" v-else>{{userInfo.desc}}</div>
        </div>
      </div>
      <div class="btn-login" v-else>
        <van-button @click="toLogin" color="#0b34ba" round size="small" type="info">请登录</van-button>
      </div>
      <div class="lists">
        <van-cell
          :title="item.title"
          :to="item.to"
          is-link
          v-for="(item, index) in lists"
          :key="index"
        />
      </div>
    </div>
    <van-popup v-model="imgShow">
      <div class="popup-img">
        <img class="auto-img" :src="userInfo.userImg" alt />
      </div>
    </van-popup>
    <van-popup v-model="bgShow" round>
      <div class="popup-bgBtn">
        <div class="popup-text">
            <p>确定要更换背景吗</p>
          </div>
          <div class="btns">
            <div class="btn btn-cancel" @click="cancelPopup">取消</div>
            <div class="btn btn-confirm" 
            @click="confirmPopup">确定
               <van-uploader class="uploader-box" :after-read="upload" />
            </div>
          </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import "../../assets/css/my.less";
import { utils } from "../../assets/js/utils";
export default {
  data() {
    return {
      isLogin: false,
      imgShow: false,
      bgShow: false,
      userInfo: {},
      maxSize: 1 * 1024 * 1024,
      lists: [
        { title: "个人资料", to: { name: "Acount" } },
        { title: "我的订单", to: { name: "Order" } },
        { title: "我的收藏", to: { name: "AllLike" } },
        { title: "收货地址", to: { name: "Address" } },
        { title: "安全中心", to: { name: "Secure" } },
      ],
    };
  },
  created() {
    this.getUserInfo();
  },
  methods: {

    toLogin(){
      this.$router.push({name:"Login"})
    },

    // 获取用户信息
    getUserInfo() {
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
           
          this.$toast.clear();
          if (result.data.code == "A001") {
            if (result.data.result.length > 0) {
              this.isLogin = true;
              this.userInfo = result.data.result[0];
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },

    //上传背景
    upload(file) {
      //获取token字符串
      let tokenString = localStorage.getItem("YXCFTK");
      if (!tokenString) {
        return;
      }

      //获取文件类型
      let type = file.file.type.split("/")[1];
      let imgTypes = ["png", "gif", "jpg", "jpeg"];
      if (imgTypes.indexOf(type) === -1) {
        this.$notify({
          message: "图片类型只支持" + imgTypes.join(","),
          color: "#fff",
          background: "#0C34BA",
        });

        return;
      }

      //大小
      if (this.maxSize < file.file.size) {
        this.$notify({
          message: "上传文件大小不能超过" + this.maxSize / 1024 + "KB",
          color: "#fff",
          background: "#0C34BA",
        });
        return;
      }

      //  

      //获取base64
      let base64 = file.content.replace(/data:image\/[A-Za-z]+;base64,/, "");

      //参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        imgType: type,
        serverBase64Img: base64,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data,
      })
        .then((result) => {
          this.$toast.clear();
           
          if (result.data.code == "I001") {
            this.userInfo.userBg = result.data.userBg;
          }
        })
        .catch((err) => {
          this.$toast.clear();
           
        });
    },

    imgShowPopup() {
      this.imgShow = true;
    },

    bgShowPopup() {
       
      this.bgShow = true;
    },

    // 取消按钮
    cancelPopup() {
      this.bgShow = false;
    },

    // 确认按钮
    confirmPopup(){
      this.bgShow = false;
    }

  },
};
</script>

<style lang="less" scoped>
</style>